<template>
    <v-app>
        <template v-if="isLoggedIn">
            <v-navigation-drawer
                    clipped
                    fixed
                    v-model="drawer"
                    app>
                <v-list two-line>

                    <v-list-tile to="/news">
                        <v-list-tile-action>
                            <v-icon>event_note</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Новости</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <v-list-tile to="/courses">
                        <v-list-tile-action>
                            <v-icon>school</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>

                            <v-list-tile-title>Курсы</v-list-tile-title>

                        </v-list-tile-content>
                    </v-list-tile>

                    <v-list-tile to="/users">
                        <v-list-tile-action>
                            <v-icon>people</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Пользователи</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                    <v-divider dark></v-divider>
                    <v-list-tile to="/me">
                        <v-list-tile-action>
                            <v-icon>account_circle</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Личный кабинет</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                </v-list>
            </v-navigation-drawer>

            <v-toolbar color="primary" dark app flat fixed clipped-left>
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
                <v-toolbar-title>Высшая школа ИТИС</v-toolbar-title>
                <v-spacer></v-spacer>

                <v-btn class="ml-3 mr-3" color="white" @click="logout" flat icon>
                    <v-icon size="40">exit_to_app</v-icon>
                </v-btn>
            </v-toolbar>

            <v-content>
                <v-container>
                    <router-view/>
                </v-container>
            </v-content>

            <v-footer height="auto" color="blue-grey lighten-5">
                <v-flex xs12 py-3 text-xs-center black--text>
                    &copy;2018 — ItisApp
                </v-flex>
            </v-footer>
        </template>

        <template v-else>
            <v-container fluid fill-height color="primary">
                <v-layout align-center justify-center>
                    <v-flex xs12 sm8 md4>
                        <v-card class="elevation-12">
                            <v-toolbar color="primary">
                                <v-toolbar-title class="white--text">Вход</v-toolbar-title>
                            </v-toolbar>


                            <v-card-text>
                                <v-form>
                                    <v-text-field v-model="userFormDto.email"
                                                  prepend-icon="person"
                                                  required
                                                  name="email" label="Email"
                                                  type="text"></v-text-field>

                                    <v-text-field prepend-icon="lock"
                                                  v-model="userFormDto.password"
                                                  required
                                                  label="Пароль" id="password"
                                                  type="password"></v-text-field>
                                </v-form>

                                <span class="red--text" v-bind:hidden="isError">
                                Что-то пошло не так((</span>
                            </v-card-text>
                            <v-card-actions>

                                <v-spacer></v-spacer>
                                <v-btn @click="login" color="primary">Войти</v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-flex>
                </v-layout>
            </v-container>
        </template>

    </v-app>
</template>

<script>

    import axios from 'axios'

    export default {
        data() {
            return {
                isLoggedIn: false,
                drawer: true,
                userFormDto: {
                    email: null,
                    token: null,
                    password: null
                },

                isError: true
            }
        },
        props: {
            source: String
        },
        created() {
            this.ping()
        },

        methods: {
            ping() {
                let user = JSON.parse(window.localStorage.getItem("user"));
                if (user == null) {
                    user = {}
                }
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                };
                axios.post(`http://localhost:8089/ping`, user, headers)
                    .then(response => {
                        this.isLoggedIn = response.data;
                        if (this.isLoggedIn) {
                            this.$router.push('/news');
                        }
                    })
                    .catch(e => {
                        this.errors.push(e);
                        window.localStorage.setItem("user", JSON.stringify({}));
                    })
            },
            logout() {
                let user = JSON.parse(window.localStorage.getItem("user"));
                if (user == null) {
                    user = {}
                }
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };

                axios.post(`http://localhost:8089/logout`, user, {
                    "headers": headers
                })
                    .then(response => {
                        this.isLoggedIn = false;
                        this.$router.push('/');
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }, login() {
                // this.isLoggedIn = true

                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                };

                axios.post(`http://localhost:8089/login`, this.userFormDto, {
                    "headers": headers
                })
                    .then(response => {
                        let user = response.data;
                        this.isError = true;
                        window.localStorage.setItem("user", JSON.stringify(user));

                        if (user.token != null) {
                            this.isLoggedIn = true;
                            // this.$router.push('/news');
                        }
                    })
                    .catch(e => {
                        this.errors.push(e);
                        this.isError = false
                    })
            }
        }
    }
</script>



