<template>
    <div>
        <p class="headline primary--text">Добавить новый предмет</p>

        <form class="mt-3">
            <v-text-field
                    v-model="courseDto.name"
                    label="Заголовок"
                    :error-messages="errors.collect('name')"
                    v-validate="'required'"
                    data-vv-name="name"
                    required>
            </v-text-field>

            <v-text-field
                    v-model="courseDto.info"
                    label="Информация"
                    multi-line
                    rows="1"
                    auto-grow>
            </v-text-field>

            <v-btn color="primary" class="mt-5 right" @click="submit">Сохранить</v-btn>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data: () => ({

            courseDto: {
                id: null,
                name: '',
                info: ''
            },

            dictionary: {
                custom: {
                    name: {
                        required: () => 'Введите название предмета',
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    let user = JSON.parse(window.localStorage.getItem("user"));
                    let headers = {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': user.token.toString()
                    };
                    axios.post(`/courses/api/add`, this.courseDto, {
                        "headers": headers
                    })
                        .then(response => {
                            this.courseDto = response.data;
                            if (this.courseDto.id != null) {
                                this.$router.push('/courses');
                            }
                        })
                        .catch(e => {
                            this.errors.push(e)
                        })
                }
            }
        }, created() {
            let user = JSON.parse(window.localStorage.getItem("user"));
            if (user.role == 'DEAN')
                this.$router.push('/courses');
            else if (this.$route.params.id != null) {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/courses/api/single/${ this.$route.params.id}`, {
                    "headers": headers
                })
                    .then(response => {
                        this.courseDto = response.data
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        }
    }
</script>



