<template>
    <div><p class="headline primary--text">Редактировать</p>

        <form class="mt-3">
            <v-text-field
                    v-model="userDto.name"
                    :error-messages="errors.collect('name')"
                    v-validate="'required'"
                    data-vv-name="name"
                    label="Имя">
            </v-text-field>
            <v-text-field
                    v-model="userDto.surname"
                    :error-messages="errors.collect('surname')"
                    v-validate="'required'"
                    data-vv-name="surname"
                    label="Фамилия">
            </v-text-field>
            <v-text-field
                    :error-messages="errors.collect('email')"
                    v-validate="'required'"
                    data-vv-name="email"
                    input="email"
                    v-model="userDto.email"
                    label="Email">
            </v-text-field>


            <p>Password</p>
            <vue-password v-model="userDto.password"
                          :error-messages="errors.collect('password')"
                          v-validate="'required'"
                          data-vv-name="password">

                <template slot="password-toggle" scope="props">
                    <button class="VuePassword__Toggle white--text primary"
                            type="button"
                            v-on:click="props.toggle"
                            v-text="props.type === 'password' ? 'Показать' : 'Скрыть'">
                    </button>
                </template>
            </vue-password>


            <v-btn class="mt-5 right" color="primary" @click="submit">Сохранить</v-btn>
            <v-btn class="mt-5 right" color="primary" to="/me">Отмена</v-btn>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'
    import VuePassword from "vue-password/src/components/VuePassword";

    export default {
        components: {VuePassword},
        data: () => ({
            userDto: {
                name: '',
                surname: '',
                id: null,
                role: '',
                password: '',
                email: ''
            },
            dictionary: {
                custom: {
                    name: {
                        required: () => 'Введите имя',
                    },
                    surname: {
                        required: () => 'Введите фамилию',
                    },
                    password:{
                        required: () => 'Введите пароль'
                    }
                    ,
                    email:{
                        required: () => 'Введите email'
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    let user = JSON.parse(window.localStorage.getItem("user"));
                    let headers = {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': user.token.toString()
                    };
                    axios.post(`/users/api/add`, this.userDto, {
                        "headers": headers
                    })
                        .then(response => {
                            this.userDto = response.data;
                            if (this.newsDto.id != null) {
                                this.$router.push('/me');
                            }
                        })
                        .catch(e => {
                            this.errors.push(e)
                        })
                }
            }
        }, created() {
            let user = JSON.parse(window.localStorage.getItem("user"));
            if (user.role != 'ADMIN')
                this.$router.push('/users');
            else {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/users/api/single/${ this.$route.params.id}`, {
                    "headers": headers
                })
                    .then(response => {
                        this.userDt = response.data
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        }
    }
</script>




