<template>
    <div id="news">

        <v-btn v-bind:hidden="notAdmin" color="blue darken-3" to="/users/add" class="ml-3" dark>Добавить</v-btn>
        <v-btn color="blue darken-3" @click="getAllNews(currentPage)" class="ml-3 right" dark>Обновить</v-btn>


        <!--News list-->
        <v-card class="mt-3 ml-3">
            <v-list three-line v-for="(item, index) in users" :key="index">
                <template>
                    <v-list-tile>

                        <v-list-tile-content class="mt-0">
                            <v-list-tile-title>{{item.name}} {{item.surname}}</v-list-tile-title>
                            <v-list-tile-sub-title>{{item.username}}</v-list-tile-sub-title>
                            <v-list-tile-sub-title>Email: {{item.email}}</v-list-tile-sub-title>
                        </v-list-tile-content>



                        <v-list-tile-avatar class="mt-0 right" v-bind:hidden="notAdmin">
                            <v-layout column >
                                <v-flex xs12 sm3>
                                    <v-btn @click="deleteUsers(index, item.id)" flat icon
                                           color="grey darken-4">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                </v-flex>
                                <v-flex xs12 sm3>
                                    <v-btn v-bind:to="getEdit(item.id)" flat icon color="blue darken-2">
                                        <v-icon>edit</v-icon>
                                    </v-btn>
                                </v-flex>
                            </v-layout>
                        </v-list-tile-avatar>


                    </v-list-tile>
                    <v-divider class="mt-0"/>
                </template>
            </v-list>


            <v-layout class="right mt-3" row>
                <v-btn @click="previousPage" icon color="primary">
                    <v-icon>navigate_before</v-icon>
                </v-btn>

                <h1 class="mt-0 ml-3 mr-3">{{currentPage + 1}}</h1>
                <v-btn @click="nextPage" icon color="primary">
                    <v-icon>navigate_next</v-icon>
                </v-btn>
            </v-layout>


        </v-card>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";
    import axios from 'axios';
    import VPaginator from "vuejs-paginator-axios/src/VPaginator";
    import NewsEdit from "./NewsEdit";

    export default {
        components: {NewsEdit, VPaginator, Icon},
        data() {
            return {
                users: [],

                currentPage: 0,
                notAdmin: true
            }

        },
        created() {
            this.refreshUsers();
            if (user.role == 'ADMIN')
                this.notAdmin = false;
        },
        methods: {
            deleteUsers(index, id) {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                this.users.splice(index, 1);
                axios.delete(`/users/api/delete/${id}`, {
                    "headers": headers
                }).catch(e => {
                    this.errors.push(e)
                });
                this.getAllUsers(this.currentPage)
            },

            getEdit(id) {
                return "/users/edit/" + id;
            },

            getAllUsers(page) {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/users/api/${page}`, {
                    "headers": headers
                })
                    .then(response => {
                        this.news = response.data;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            },
            refreshUsers() {
                this.currentPage = 0;
                this.getAllUsers(this.currentPage);
            },
            previousPage() {
                if (this.currentPage != 0) {
                    this.currentPage--;
                    this.getAllUsers(this.currentPage);
                }
            },
            nextPage() {
                this.currentPage++;
                this.getAllUsers(this.currentPage);
            }

        }
    }

</script>
<style>

</style>




