import Vue from 'vue'
import Router from 'vue-router'
import News from './components/News'
import Courses from './components/Courses'
import Me from './components/Me'
import User from './components/Users'
import EditNews from './components/NewsEdit'
import EditUser from './components/UserEdit'
import EditCourse from './components/CourseEdit'
import MeEdit from './components/MeEdit'


Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/me',
            name: 'me',
            title:'Личный кабинет',
            component: Me
        },
        {
            path: '/news',
            name: 'news',
            title:'Новости',
            component: News
        },
        {
            path: '/courses',
            name: 'courses',
            title:'Предметы',
            component: Courses
        },
        {
            path: '/users',
            name: 'user',
            title:'Пользователи',
            component: User
        },
        {
            path: '/news/edit/:id',
            name: 'news edit',
            title:'Редактировать новость',
            component: EditNews,
            props: true
        },
        {
            path: '/news/add',
            name: 'news add',
            title:'Добавить новость',
            component: EditNews
        },
        {
            path: "/",
            title:'Itis App',
            name: 'main'
        },
        {
            path: '/courses/add',
            name: 'courses add',
            title:'Добавить предмет',
            component: EditCourse
        },
        {
            path: '/courses/edit/:id',
            name: 'courses edit',
            title:'Редактировать предмет',
            component: EditCourse
        },
        {
            path: '/users/edit/:id',
            name: 'user edit',
            title:'Редактировать пользователя',
            component: EditUser
        },
        {
            path: '/users/add',
            name: 'users add',
            title:'Добавить пользователя',
            component: EditUser
        },
        {
            path: '/me/edit',
            name: 'me edit',
            title:'Редактировать личную информацию',
            component: MeEdit
        }
    ]
})


