<template>
    <div id="news">

        <v-btn color="blue darken-3" to="/news/add" class="ml-3" dark>Добавить</v-btn>
        <v-btn color="blue darken-3" @click="hideFilter" class="ml-3" dark>Фильтр</v-btn>
        <v-btn color="blue darken-3" @click="getAllNews(currentPage)" class="ml-3 right" dark>Обновить</v-btn>
        <v-btn color="red darken-3" @click="refreshNews" class="right" v-bind:hidden="removeFilters"
               dark>Сбросить фильтры
        </v-btn>

        <!--Filter block-->
        <div class="mt-3 mb-3 ml-3 mb-5" v-bind:hidden="isFilter">
            <h1 class="headline primary--text">Добавить фильтр</h1>
            <v-card>

                <v-layout row class="ml-3 mt-3">
                    <v-flex xs5 sm5>
                        <v-menu
                                ref="from_"
                                lazy
                                :close-on-content-click="false"
                                v-model="from_"
                                transition="scale-transition"
                                offset-y
                                full-width
                                :nudge-right="40"
                                min-width="290px"
                                :return-value.sync="newsFilter.fromDate">
                            <v-text-field
                                    v-model="newsFilter.fromDate"
                                    slot="activator"
                                    label="С"
                                    prepend-icon="event"
                                    readonly>
                            </v-text-field>

                            <v-date-picker v-model="newsFilter.fromDate"
                                           @input="$refs.from_.save(newsFilter.fromDate)"></v-date-picker>
                        </v-menu>
                    </v-flex>
                    <v-flex xs5 sm5>
                        <v-menu
                                class="ml-3"
                                ref="to_"
                                lazy
                                :close-on-content-click="false"
                                v-model="to_"
                                transition="scale-transition"
                                offset-y
                                full-width
                                :nudge-right="40"
                                min-width="290px"
                                :return-value.sync="newsFilter.toDate">
                            <v-text-field
                                    v-model="newsFilter.toDate"
                                    slot="activator"
                                    label="до"
                                    readonly>
                            </v-text-field>
                            <v-date-picker v-model="newsFilter.toDate"
                                           @input="$refs.to_.save(newsFilter.toDate)"></v-date-picker>
                        </v-menu>
                    </v-flex>
                </v-layout>

                <v-flex xs10 sm10 class="ml-3">
                    <v-select
                            :items="years"
                            v-model="newsFilter.year"
                            clearable
                            label="Год обучения"
                            single-line
                            multiple
                            autocomplete
                            prepend-icon="grade"
                    ></v-select>
                </v-flex>

                <v-btn class="mb-3" @click="filterNews">Ок</v-btn>
                <v-btn class="mb-3" @click="clearFilter">Сбросить</v-btn>

            </v-card>
        </div>


        <!--News list-->
        <v-card class="mt-3 ml-3">
            <v-list three-line v-for="(item, index) in news" :key="index">
                <template>
                    <v-list-tile>
                        <v-list-tile-avatar v-if="!isDean" class="mt-0">
                            <v-avatar color="blue darken-4" size="25">
                                <span class="white--text subheader">{{item.year}} </span>
                            </v-avatar>
                        </v-list-tile-avatar>

                        <v-list-tile-content class="mt-0">
                            <v-list-tile-title v-html="item.title"></v-list-tile-title>
                            <v-list-tile-title v-if="isDean">Курс: {{item.course.name}}
                            </v-list-tile-title>
                            <v-list-tile-sub-title v-html="item.newsText"></v-list-tile-sub-title>
                            <v-list-tile-sub-title>Дедлайн: {{item.deadline}}</v-list-tile-sub-title>
                        </v-list-tile-content>

                        <v-list-tile-avatar class="mt-0 right">
                            <v-layout column>
                                <v-flex xs12 sm3>
                                    <v-btn @click="deleteNews(index, item.id)" flat icon color="grey darken-4">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                </v-flex>
                                <v-flex xs12 sm3>
                                    <v-btn v-bind:to="getEdit(item.id)" flat icon color="blue darken-2">
                                        <v-icon>edit</v-icon>
                                    </v-btn>
                                </v-flex>
                            </v-layout>
                        </v-list-tile-avatar>


                    </v-list-tile>
                    <v-divider class="mt-0"/>
                </template>
            </v-list>


            <v-layout class="right mt-3" row>
                <v-btn @click="previousPage" icon color="primary">
                    <v-icon>navigate_before</v-icon>
                </v-btn>

                <h1 class="mt-0 ml-3 mr-3">{{currentPage + 1}}</h1>
                <v-btn @click="nextPage" icon color="primary">
                    <v-icon>navigate_next</v-icon>
                </v-btn>
            </v-layout>


        </v-card>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";
    import axios from 'axios';
    import VPaginator from "vuejs-paginator-axios/src/VPaginator";
    import AddNews from "./NewsEdit";

    export default {
        components: {AddNews, VPaginator, Icon},
        data() {
            return {
                removeFilters: true,
                news: [],

                currentPage: 0,
                isFilter: true,
                newsFilter: {
                    fromDate: null,
                    toDate: null,
                    year: []
                },

                isDean: true,
                from_: false,

                to_: false,
                years: [0, 1, 2, 3, 4],

            }

        },
        created() {
            this.refreshCourses();
            let user = JSON.parse(window.localStorage.getItem("user"));
            if (user.role != 'PROFESSOR' || user.role != 'ADMIN')
                this.isDean = false;
        },
        methods: {
            deleteNews(index, id) {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                this.news.splice(index, 1);
                axios.delete(`/news/api/delete/${id}`, {
                    "headers": headers
                }).catch(e => {
                    this.errors.push(e)
                });
                this.getAllNews(this.currentPage)
            },
            hideFilter() {
                this.isFilter = !this.isFilter
            },
            getEdit(id) {
                return "/news/edit/" + id;
            },
            filterNews() {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };

                axios.post(`/news/api/filter`, this.newsFilter, {
                    "headers": headers
                })
                    .then(response => {
                        this.news = response.data;
                        this.removeFilters = false;
                        this.isFilter = true;
                    })
                    .catch(e => {
                        this.errors.push(e)

                    })
            },
            getAllNews(page) {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/news/api/${page}`, {
                    "headers": headers
                })
                    .then(response => {
                        this.news = response.data;
                        this.removeFilters = true;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            },
            clearFilter() {
                let newsFilter = {
                    fromDate: null,
                    toDate: null,
                    year: []
                };

                this.from_ = false;
                this.to_ = false;
                this.newsFilter = newsFilter;
            },
            refreshCourses() {
                this.currentPage = 0;
                this.getAllNews(this.currentPage);
            },
            previousPage() {
                if (this.currentPage != 0) {
                    this.currentPage--;
                    this.getAllNews(this.currentPage);
                }
            },
            nextPage() {
                this.currentPage++;
                this.getAllNews(this.currentPage);
            }

        }
    }

</script>
<style>

</style>




