<template>
    <div><p class="headline primary--text">Редактировать новость</p>

        <form class="mt-3">
            <v-text-field
                    v-model="newsDto.title"
                    label="Заголовок"
                    :error-messages="errors.collect('title')"
                    v-validate="'required'"
                    data-vv-name="title"
                    required>

            </v-text-field>

            <v-text-field
                    v-model="newsDto.newsText"
                    label="Текст"
                    v-validate="'required'"
                    multi-line
                    rows="1"
                    :error-messages="errors.collect('newsText')"
                    auto-grow
                    data-vv-name="newsText"
                    required>
            </v-text-field>

            <v-menu
                    ref="deadline"
                    lazy
                    :close-on-content-click="false"
                    transition="scale-transition"
                    offset-y
                    full-width
                    :nudge-right="40"
                    min-width="290px"
                    :return-value.sync="newsDto.deadline">
                <v-text-field
                        v-model="newsDto.deadline"
                        slot="activator"
                        :error-messages="errors.collect('deadline')"
                        data-vv-name="deadline"
                        label="Дедлайн"
                        v-validate="'required'"
                        prepend-icon="event"
                        readonly>
                </v-text-field>

                <v-date-picker v-model="newsDto.deadline"
                               @input="$refs.deadline.save(newsDto.deadline)"></v-date-picker>

            </v-menu>

            <v-flex xs12 sm6 v-else v-if="isDean">
                <v-select
                        :items="years"
                        v-model="newsDto.year"
                        label="Год обучения"
                        single-line
                        clearable
                        autocomplete
                        prepend-icon="grade"
                ></v-select>
            </v-flex>

            <v-flex xs12 sm6 v-else>
                <v-select
                        :items="courses"
                        item-text="name"
                        v-model="newsDto.course"
                        label="Предмет"
                        single-line
                        clearable
                        autocomplete
                        prepend-icon="grade"
                ></v-select>
            </v-flex>




            <v-btn class="mt-5 right" color="primary" @click="submit">submit</v-btn>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data: () => ({
            newsDto: {
                newsText: '',
                title: '',
                deadline: '',
                year: 0,
                id: null,
                course: null
            },
            years: [1, 2, 3, 4],
            deadline: false,
            isDean: true,
            courses: [],
            valid: true,
            requiredRules: [
                v => !!v || 'Это поле обязательно для заполнения'
            ],
            dictionary: {
                custom: {
                    title: {
                        required: () => 'Введите заголовок',
                    },
                    newsText: {
                        required: () => 'Введите текст',
                    },
                    date: {
                        required: () => 'Выберите дату дедлайна',
                    }, deadline: {
                        required: () => 'Выберите дату дедлайна',
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        created() {
            let user = JSON.parse(window.localStorage.getItem("user"));

            if (user.role == 'PROFESSOR') {
                this.isDean = false;
                this.getCourses();
            }

            if (this.$route.params.id != null) {
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/news/api/single/${ this.$route.params.id}`, {
                    "headers": headers
                })
                    .then(response => {
                        this.newsDto = response.data
                    })
                    .catch(e => {
                        this.errors.push(e)
                    });
            }
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    let user = JSON.parse(window.localStorage.getItem("user"));
                    let headers = {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json',
                        'Authorization': user.token.toString()
                    };
                    axios.post(`/news/api/add`, this.newsDto, {
                        "headers": headers
                    })
                        .then(response => {
                            this.newsDto = response.data;
                            if (this.newsDto.id != null) {
                                this.$router.push('/news');
                            }
                        })
                        .catch(e => {
                            this.errors.push(e)

                        })
                }
            },
            getCourses() {
                let user = JSON.parse(window.localStorage.getItem("user"));
                let headers = {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': user.token.toString()
                };
                axios.get(`/courses/api`, {
                    "headers": headers
                })
                    .then(response => {
                        this.courses = response.data;
                        if (this.courses.length > 1) {
                            this.newsDto.course = this.courses[0]
                        }
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        }
    }
</script>

<codepen-resources lang="json">
    {
    "js": ["https://unpkg.com/vee-validate@latest/dist/vee-validate.js"]
    }
</codepen-resources>



